@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:'Roboto', sans-serif}.carousel .carousel-item{height:600px}.carousel-item img{position:absolute;object-fit:cover;top:0;left:0;min-height:600px}header{background:#a5d6a7}header .navbar-brand{font-size:2rem}main{background:#e8f5e9}

